import React, { Component } from "react";

import "./App.css";
import ReactTable from "react-table";
import 'react-table/react-table.css'
export default class TrigTablePlus extends React.Component {
  
   constructor(props) {
    super(props);
   }
   componentDidMount(){
    
   }
  render() {
    return(
<div className="trigTable">
  
    <span><div>Radians </div>{(this.props.soSpecial)?<div><input step={0.001} className="special" type="Text"  onFocus={e => e.target.select()}  
    value=
    {((this.props.angle.angle<0)?"-":"")+((this.props.frac.n!==1)?this.props.frac.n:"")+this.props.specialText.pi+
    ((this.props.frac.d!==1)?"/"+
    this.props.frac.d:"")}  
    onChange={(event)=>{this.props.handleChange(event,'ang')}}  /></div>:<div><input step={0.001} className="trigForm" type="Number"  onFocus={e => e.target.select()}  value={this.props.angle.angle}  onChange={(event)=>{this.props.handleChange(event,'ang')}}  /></div>}</span>
    <span><div>Degrees </div>
  <div><input className="trigForm" step={0.1} type="Number" onFocus={e => e.target.select()} value={this.props.angle.angleInDegrees} onChange={(event)=>{this.props.handleChange(event,'deg')}}  /></div></span>
 
  {(this.props.soSpecial)?<span><div className="sin">Sine </div><div><input  className="special" type="Text"  onFocus={e => e.target.select()} value={this.props.soSpecial.sin} onChange={(event)=>{this.props.handleChange(event,'sin')}} /></div>
    </span>:
  <span><div className="sin">Sine </div><div><input className="trigForm" type="Number" step={0.001} onFocus={e => e.target.select()} value={this.props.angle.sine} onChange={(event)=>{this.props.handleChange(event,'sin')}} /></div>
    </span>}
  
    {(this.props.soSpecial)?<span><div className="cos">Cosine </div><div><input  className="special" type="Text"  onFocus={e => e.target.select()} value={this.props.soSpecial.cos} onChange={(event)=>{this.props.handleChange(event,'cos')}} /></div>
    </span>:
  <span><div className="cos">Cosine </div><div><input className="trigForm" type="Number" step={0.001} onFocus={e => e.target.select()} value={this.props.angle.cosine} onChange={(event)=>{this.props.handleChange(event,'cos')}} /></div>
    </span>}
  <span>
  <div className="tan">Tangent </div>
    <div><input className={(Math.abs(this.props.angle.tangent)===this.props.huge)?"infinity":"trigForm"} type="Number" step={0.001} onFocus={e => e.target.select()} value={this.props.angle.tangent} onChange={(event)=>{this.props.handleChange(event,'tan')}} /></div> 
  </span>
  <span>
  <div className="csc">Cosecant </div>
    <div ><input className={(Math.abs(this.props.angle.cosecant)===this.props.huge)?"infinity":"trigForm"} type="Number" step={0.001} onFocus={e => e.target.select()} value={this.props.angle.cosecant} onChange={(event)=>{this.props.handleChange(event,'csc')}} /></div> 
  </span>
  <span>
  <div className="sec">Secant </div>
    <div ><input className={(Math.abs(this.props.angle.secant)===this.props.huge)?"infinity":"trigForm"} type="Number" step={0.001} onFocus={e => e.target.select()} value={this.props.angle.secant} onChange={(event)=>{this.props.handleChange(event,'sec')}}/></div> 
  </span><span>
  <div className="cot">Cotangent </div>
    <div ><input className={(Math.abs(this.props.angle.cotangent)===this.props.huge)?"infinity":"trigForm"} type="Number" step={0.001}  onFocus={e => e.target.select()} value={this.props.angle.cotangent} onChange={(event)=>{this.props.handleChange(event,'cot')}}/></div> 
  </span>
  <span>
  <div  ><i>sig. figs. {this.props.sigFigs}</i></div>
    <div ><input type="range" id="sigFigs" name="sigFigs" value={this.props.sigFigs}
     onChange={(event)=>{this.props.handleChange(event,'sf')}}      min="1" max="12" /></div> 
  </span>
  
 

</div>)


  }
}

 